.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

header {
  font-size: 3rem;
  border-bottom: 2px solid #fff;
  margin-bottom: 2rem;
}

@media (max-width: 770px) {
  header {
    font-size: 2rem;
    border-bottom: 2px solid #fff;
    margin-bottom: 2rem;
  }
}
